/* ----------------------------------------------------------------
	CSS Specific to the Seo Demo

	Some of the CSS Codes have been modified from the Original
	style.css File to match the Styling of this Demo

-----------------------------------------------------------------*/


/* Root Settings
-----------------------------------------------------------------*/

:root {
    --themecolor: #f04141;
    --buttonthemecolor: #d40d0d;
    --badgethemecolor: #f04141;
    --basefontsize: 16px;
    --fontfamily: 'Poppins', sans-serif;
    --headerboxshadow: 0 0 10px 5px rgba(0, 0, 0, 0.05);
    ;
    --menufontsize: 15px;
}


/* Buttons
-----------------------------------------------------------------*/

.button {
    text-shadow: none;
    font-weight: 600;
}

.button:hover {
    transform: translateY(-1px);
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.05);
}

#header .button {
    display: none;
}


/* Top Links
-----------------------------------------------------------------*/

.top-links ul li {
    border-left: 0;
}

.top-links li a img {
    position: relative;
    display: inline-block;
    border-radius: 20px;
    width: 20px;
    height: 20px;
    top: -2px;
    margin-right: 8px;
    border: 1px solid #FFF;
    background-color: #555;
}

.dark #top-social li a {
    color: #555;
}

.border-bottom {
    border-color: #EEE !important;
}

.feature-box h3 span {
    font-size: var(--basefontsize);
}

.heading-block>p {
    font-size: var(--basefontsize);
    color: #777;
}

.slider-title p,
.feature-box.fbox-center p {
    font-size: calc( var(--basefontsize) - 1px);
    line-height: 1.7;
    color: #777;
    letter-spacing: 0;
}

.play-icon i {
    width: 70px;
    height: 70px;
    line-height: 71px;
    font-size: 24px;
    background: linear-gradient(60deg, #f15c5c, #f34a4a, #e63131, #d40d0d);
    color: #FFF;
    text-align: center;
    border-radius: 50%;
    padding-left: 4px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
    transition: transform .3s ease;
    animation: animdgradient 3s ease alternate infinite;
    background-size: 300% 300%;
}

@keyframes animdgradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

.play-icon:hover i {
    transform: scale(1.1);
}

#oc-clients .oc-item img {
    height: 32px;
}


/* Pricing Switcher
-----------------------------------------------------------------*/

.pricing-tenure-switcher {
    position: relative;
}

.pricing-tenure-switcher .pts-left,
.pricing-tenure-switcher .pts-right,
.pricing-tenure-switcher .pts-switcher {
    display: inline-block;
    margin: 0 10px;
    height: 30px;
    overflow: hidden;
}

.pricing-tenure-switcher .pts-left,
.pricing-tenure-switcher .pts-right {
    font-size: var(--basefontsize);
    font-weight: 600;
    color: #AAA;
    line-height: 30px;
}

.pricing-tenure-switcher .pts-switch-active {
    color: #333;
}

.pricing-tenure-switcher .pts-switcher label {
    margin-bottom: 0;
}

input.switch-toggle-round+label:before,
input.switch-toggle-round+label {
    border-radius: 4px;
    background-color: var(--themecolor);
}

.price-discount {
    position: absolute;
    display: block;
    top: 0;
    left: 50%;
    margin-top: -15px;
    margin-left: 65px;
    background: url('../images/icons/discount.svg') no-repeat 0 0;
    width: 60px;
    height: 39px;
}

input.switch-toggle-round+label:after {
    border-radius: 4px
}


/* Pricing Boxes
-----------------------------------------------------------------*/

.pricing-box {
    border-color: rgba(17, 18, 18, .1);
    text-align: left;
    padding: 20px;
    background-color: #FFF;
    box-shadow: none;
    margin-right: -1px;
}


/* Pricing Boxes - Title
-----------------------------------------------------------------*/

.pricing-title {
    padding: 0;
    background-color: transparent;
    border-bottom: 0;
}

.pricing-title h3 {
    margin: 0;
    font-size: 21px;
    letter-spacing: 0;
    color: #555;
    text-transform: none;
}

.pricing-title span {
    font-size: 14px;
    text-transform: capitalize;
}


/* Pricing Boxes - Price
-----------------------------------------------------------------*/

.pricing-price {
    position: relative;
    font-weight: 500;
}

.pricing-price:after {
    content: none;
}

.pricing-price span.price-tenure {
    display: block;
    font-size: 15px;
    font-weight: normal;
    color: #AAA;
    text-transform: lowercase;
}


/* Pricing Boxes - Features
-----------------------------------------------------------------*/

.pricing-features ul {
    padding: 0 0 20px 0;
}

.pricing-features li {
    padding: 7px 0;
    font-size: 14px;
}

.pricing-features li i {
    position: relative;
    top: 2px;
    font-size: var(--basefontsize);
}


/* Pricing Boxes - Action
-----------------------------------------------------------------*/

.pricing-action {
    padding: 0;
    text-align: center;
}


/* Badge
-----------------------------------------------------------------*/

.badge {
    padding: 8px;
    font-size: 12px;
    margin-bottom: 15px;
    border-radius: 2px;
    font-weight: 500;
    line-height: .8;
}

.badge.badge-default {
    border: 1px solid var(--themecolor);
    color: var(--themecolor);
}


/* Testimonials Carousel
-----------------------------------------------------------------*/

.testimonials-carousel .owl-stage {
    padding-top: 40px
}

.testimonials-carousel .owl-item {
    opacity: .6;
    transition: transform .3s ease;
    transform: scale(0.8);
}

.testimonials-carousel .owl-item.active.center {
    opacity: 1;
    transform: scale(1);
}

.testimonial {
    border: 0;
    box-shadow: 0 0 35px rgba(140, 152, 164, 0.2);
    border-radius: 0.25rem;
    padding: 25px;
}

.testi-image {
    float: none;
    margin: -55px auto 0;
    margin-bottom: 20px;
}

.testi-content p {
    text-align: center;
    font-style: normal;
    font-family: var(--fontfamily);
    font-size: var(--basefontsize);
}

.testi-meta {
    text-align: center;
    margin-top: 20px;
}


/* Blog
-----------------------------------------------------------------*/

.entry-title h3 {
    font-size: 17px;
    margin-bottom: 13px;
}

.entry .entry-title h3 a {
    color: #222 !important
}

.entry-meta li {
    font-style: normal;
}

.entry-meta li:before {
    content: "\00b7";
}

.entry-meta li a {
    font-weight: 500;
}


/* Footer
-----------------------------------------------------------------*/

.footer-big-contacts {
    color: #333;
    font-size: 20px;
    font-weight: bold;
    letter-spacing: 1px;
}

.footer-big-contacts span {
    display: block;
    font-size: 10px;
    font-weight: 400;
    text-transform: uppercase;
    color: #888;
    letter-spacing: 2px;
}

.dark .footer-big-contacts {
    color: rgba(255, 255, 255, 0.8);
}

.subscribe-widget .form-control {
    height: calc(2.25rem + 4px) !important;
}

.subscribe-widget .button {
    transform: none;
    box-shadow: none;
}

.promo-section:before {
    content: '';
    position: absolute;
    top: -17%;
    left: 45%;
    width: 80%;
    height: 451px;
    background-size: 1280px 451px;
    background-repeat: no-repeat;
    background-image: url('../images/hero/hero-3.svg');
}


/* Responsive Device more than 992px (.device-md >)
-----------------------------------------------------------------*/

@media (min-width: 992px) {
    #header.transparent-header.floating-header+#slider {
        top: -165px;
        margin-bottom: -165px;
    }
    #header.transparent-header.floating-header {
        margin-top: 25px;
    }
    #header:not(.sticky-header) #header-wrap .container {
        box-shadow: var(--headerboxshadow)
    }
    #header.transparent-header+#page-title.page-title-parallax .container {
        padding-top: 80px;
    }
    #primary-menu ul li>a {
        padding-top: 29px;
        padding-bottom: 29px;
        text-transform: none;
        letter-spacing: 0;
        font-size: var(--menufontsize);
        font-weight: 600;
    }
    #top-search {
        margin-top: 30px;
        margin-bottom: 30px;
    }
    #header,
    #header-wrap,
    #logo img {
        height: 80px;
    }
    #header.sticky-header:not(.static-sticky),
    #header.sticky-header:not(.static-sticky) #header-wrap,
    #header.sticky-header:not(.static-sticky):not(.sticky-style-2):not(.sticky-style-3) #logo img {
        height: 70px;
    }
    #header.sticky-header:not(.static-sticky) #primary-menu>ul>li>a {
        padding-top: 24px;
        padding-bottom: 24px;
    }
    #header.sticky-header:not(.static-sticky) #top-search {
        margin-top: 25px !important;
        margin-bottom: 25px !important;
    }
    #header .button {
        display: inline-block;
        margin: 20px 0;
        text-transform: none;
        letter-spacing: 0;
        font-size: calc( var(--basefontsize) - 1px);
        -webkit-transition: all .4s ease;
        -o-transition: all .4s ease;
        transition: all .4s ease;
    }
    #header.sticky-header .button {
        margin-top: 15px;
        margin-bottom: 15px;
    }
    /* Topbar
	-----------------------------------------------------------------*/
    #top-bar.transparent-topbar {
        display: block;
        z-index: 299;
        border-bottom: 0;
        padding-top: 15px;
    }
    #top-bar.transparent-topbar {
        background: transparent !important;
    }
    /* Slider Text
	-----------------------------------------------------------------*/
    .slider-title h2 {
        font-size: 50px;
        line-height: 1.3;
        letter-spacing: -1px;
        font-weight: 700;
        margin-bottom: 26px;
    }
    .slider-title h3 {
        font-size: 18px;
        color: #AAA;
    }
}
